	import { Config } from '../../config.js';
	import {Adv} from '@/model/adv.js';
	import {System} from '@/model/system.js';
	import {Product} from '@/model/product.js';
	var jweixin = require('jweixin-module');
	var adv = new Adv();
	var system = new System();
	var product = new Product();
	import uniGrid from "@/components/uni-grid/uni-grid.vue"
	import jhxProductList from "@/components/jhx-product-list/jhx-product-list.vue"
	import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
    export default {
		
		components: {uniGrid,uniGridItem},
        data() {
            return {
                banner:[],
				nav:[],
				productList:[]
            }
        },
        onLoad() {
			this.getNav();
            this.getBanner();
			this.getProduct(); 
				this.login()
			
        },
		methods: {
			//幻灯片
			getBanner:function(){
				var that = this;
				var param = {'num':8,'key':'index'};
				adv.getAdv(param,(data) => {
					that.$data.banner=data.items;
				});
			},
			//导航
			getNav:function(){
				var that = this;
				var param = {'num':8,'key':'mobile_header'};
				system.getNav(param,(data) => {
					that.$data.nav=data.items;
				});
			},
			//产品
			getProduct:function(){
				var that = this;
				var param = {'num':8,'key':'mobile_header'};
				product.lists(param,(data) => {
					that.$data.productList=data.items;
				});
			},
			// 发送code 获取信息
			login:function() {
				uni.getProvider({
					service: 'oauth',
					success: function(res) {
						console.log(res.provider);
						//支持微信、qq和微博等
						if (~res.provider.indexOf('weixin')) {
							uni.login({
								provider: 'weixin',
								success: function(loginRes) {
									console.log('-------获取openid(unionid)-----');
									console.log(JSON.stringify(loginRes));
									// 获取用户信息
									uni.getUserInfo({
										provider: 'weixin',
										success: function(infoRes) {
											console.log('-------获取微信用户所有-----');
											console.log(JSON.stringify(infoRes.userInfo));
										}
									});
								}
							});
						}
					}
				});
			}
		}
    }
