 */
export default {
	name: 'JhxProductList',
	props: {
		productList:Array,
		column:Number
	},
	data() {
		return {
			col:'col-'+this.column
		}
	},
	created() {
		
	},
	beforeDestroy() {
		
	},
	methods: {
		_onClick() {
			this.grid.change({
				detail: {
					index: this.index
				}
			})
		}
	}
}
